.nav-collapse .active a,
.nav-collapse a:active {
    color: #9f449b;
}
.logo {
    padding-top: 3%;
    padding-left: 3%;
    padding-bottom: 3%;
}
.nav-collapse {
    ul {
        width: 100%;
    }
    a {
        -webkit-tap-highlight-color: rgba(0,0,0,0);
        text-decoration: none;
        color: $diffrent-text-color;
    }
}
@media (max-width: $mobile) {

  .nav-collapse {
      display: none;
      ul {
          padding: 0;
          margin: 0 auto;
          position: static;
      }
      li {
          border-top: 1px solid $diffrent-text-color;
          padding: 10px;
          text-align: center;
          margin: 10px;
          list-style: none;
      }
      a {
          text-transform: uppercase;
          font-size: $font-size-s;
          width: 100%;
      }
      a:hover {
          color: $color;
          font-weight: bold;
      }
  }
  #hmb_menu {
      float: right;
      padding-top: 4%;
      width: 60px;
      height: 45px;
      position: relative;
      margin-right: 5%;
      margin-top: 3%;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: 0.5s ease-in-out;
      -moz-transition: 0.5s ease-in-out;
      -o-transition: 0.5s ease-in-out;
      transition: 0.5s ease-in-out;
      cursor: pointer;
      span {
          display: block;
          position: absolute;
          height: 6px;
          width: 100%;
          background: $diffrent-text-color;
          border-radius: 9px;
          opacity: 1;
          left: 0;
          -webkit-transform: rotate(0deg);
          -moz-transform: rotate(0deg);
          -o-transform: rotate(0deg);
          transform: rotate(0deg);
          -webkit-transition: 0.25s ease-in-out;
          -moz-transition: 0.25s ease-in-out;
          -o-transition: 0.25s ease-in-out;
          transition: 0.25s ease-in-out;
      }
      span:nth-child(1) {
          top: 0;
      }
      span:nth-child(2),
      span:nth-child(3) {
          top: 18px;
      }
      span:nth-child(4) {
          top: 36px;
      }
  }
  #hmb_menu.open span:nth-child(1) {
      top: 18px;
      width: 0;
      left: 50%;
  }
  #hmb_menu.open span:nth-child(2) {
      -webkit-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      -o-transform: rotate(45deg);
      transform: rotate(45deg);
  }
  #hmb_menu.open span:nth-child(3) {
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
      transform: rotate(-45deg);
  }
  #hmb_menu.open span:nth-child(4) {
      top: 18px;
      width: 0;
      left: 50%;
    }
  }

