//zamkniecie media dla mobile-desktop
@media (min-width: $desktop) {
    .nav-collapse {
        float: right;
        ul {
            margin: 0;
            display: inline-block;
            list-style: none;
            text-transform: uppercase;
            padding-top: 30px;
            font-size: 18px;
        }
        li {
            display: block;
            float: left;
        }
        a {
            width: 100%;
            text-align: center;
            padding: 0.7em 1em;
            float: left;
            border-top: 0;
            margin: 0;
        }
    }
    #hmb_menu {
        display: none;
    }
}
